$backgroundColor: #1c2128;
$colorFont: #fff;
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: $backgroundColor;
  height: 100%;
  #root * {
    box-sizing: border-box;
    width: 100%;
  }
  #root {
    color: $colorFont;
    display: flex;
    flex-direction: column;
    align-items: center;

    height: 100%;
  }
  #root * {
    box-sizing: border-box;
    width: 100%;
  }
}
